<template>
    <div>
        <h1>{{id ? '编辑' : '新建'}}广告位</h1>
        <el-form label-width="120px" @submit.native.prevent="save()">
            <el-form-item label="名称">
                <el-input v-model="model.name"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="text" @click="model.items.push({})"> <i class="el-icon-plus"></i> 添加技能</el-button>
                    <el-row type="flex" style="flex-wrap: wrap">
                        <el-col :md="12" v-for="(item,i) in model.items" :key="i">
                            <el-form-item label="跳转链接(URL)">
                                <el-input v-model="item.url"></el-input>
                            </el-form-item>
                            <el-form-item label="图标" style="margin-top:0.5rem;">
                                <el-upload
                                    class="avatar-uploader"
                                    :action="$http.defaults.baseURL + '/api/upload'"
                                    :show-file-list="false"
                                    :on-success="res => $set(item,'image' ,res.url) "
                                >
                            <img v-if="item.image" :src="item.image" class="avatar">
                            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                            </el-upload>
                            </el-form-item>
                        </el-col>
                    </el-row>
            </el-form-item>
            <el-form-item>
            <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
        </el-form>
    </div>
</template>
<script>
export default {
    props:{
        id:{}
    },
    data(){
        return {
            model : {
                items: []
            },
        }
    },
    methods:{
        async save(){
            let res;console.log(res)
            if(this.id){
                res = await this.$http.put('/api/rest/ads/'+this.id,this.model);
            }else{
                res = await this.$http.post('/api/rest/ads',this.model);
            }
            this.$router.push('/ads/list')
            this.$message({
                type:'success',
                message:"保存成功"
            })
        },
        async fetch(){
            const res = await this.$http.get('/api/rest/ads/'+this.id);
            this.model = Object.assign({},this.model,res.data);
        },
    },
    created(){
        this.id && this.fetch()
    }
}
</script>
