<template>
    <div>
        <h1>管理员列表</h1>
        <el-table :data="items">
            <el-table-column prop="_id" label="ID" width="220"></el-table-column>
            <el-table-column prop="username" label="用户名"></el-table-column>
            <!-- <el-table-column prop="password" label="密码"></el-table-column> -->
            <el-table-column
                fixed="right"
                label="操作"
                width="100">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="$router.push(`edit/${scope.row._id}`)">编辑</el-button>
                        <el-button type="text" size="small" @click="remove(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
        </el-table>
    </div>
</template>
<script>
export default {
    data(){
        return {
            items:[]
        }
    },
    methods:{
        async fetch(){
            const res = await this.$http.get('/api/rest/adminusers');
            // console.log(res)
            this.items = res.data;
            console.log(this.items)
        },
        async remove(row){
            this.$confirm('是否删除?'+row.name, '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(async () => {
                const res = await this.$http.delete('/api/rest/adminusers/'+row._id)
                console.log(res)
            this.$message({
                type: 'success',
                message: '删除成功!'
            });
            this.fetch();
            }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });          
            });
        }
    },
    created(){
        this.fetch();
    }
}
</script>